@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
.nav {
  display: flex;
  justify-content: space-between;
  width: 85vw;
  margin: auto;
  font-family: "Goldman", cursive;
  padding-left: 2vw;
}
.logo {
  width: 70px;
  height: 70px;
  position: relative;
  top: 15px;
  cursor: pointer;
}
.navbar {
  flex: 2 1;
  display: flex;
  list-style: none;
}
.navbar li {
  cursor: pointer;
  padding: 0 2vw;
  height: 100px;
  font-size: 1.3vw;
  line-height: 100px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.navbar li:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}
.navbar a {
  text-decoration: none;
}
.loginBt {
  position: relative;
  text-decoration: none;
}
.loginBt li {
  padding: 0 2.5vw;
}

.screen {
  z-index: -1000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: center/cover no-repeat
    url("https://blog.kakaocdn.net/dn/bdfRwj/btqEwJs2wSb/Nt0S2iPA6dUh6h5KTgDiVk/img.jpg");
}

* {
  margin: 0;
  box-sizing: border-box;
}

